import React from 'react'
import { Link } from 'react-router-dom' 
import postal from '../Assets/IndiaPostLogo.jpg'

const Heading = () => {
    const  change_mode= () =>{
        if(document.body.style.backgroundColor == 'black'){
          document.body.style.backgroundColor = 'white';
        }else{
          document.body.style.backgroundColor = 'black';
        }
  
    }; 
  return (
    <div className="flex m-2 bg-red-500 p-3 rounded-md text-amber-300 font-bold justify-between ">
      <img src={postal} className="h-6 rounded"></img>
      <h1>Department of Posts, India</h1>
      <nav className="space-x-3">
        <Link to="/" className="text-amber-300 hover:text-gray-300">Home</Link>
        <Link to="/login" className="text-amber-300 hover:text-gray-300">Login</Link>
        <Link to="/about" className="text-amber-300 hover:text-gray-300">About</Link>
        <Link to="/contact" className="text-amber-300 hover:text-gray-300">Contact</Link>
        <button className='bg-lime-500 p-2  mt-2' onClick={change_mode}>Dark/Light Mode</button>
      </nav>
    </div>
  )
}

export default Heading
