// Make sure to run npm install @formspree/react
// For more help visit https://formspr.ee/react-help
import React from 'react';
import Heading from './Heading'
import { useForm, ValidationError } from '@formspree/react';
function Contact() {
  const [state, handleSubmit] = useForm("xdorzper");
  if (state.succeeded) {
      return     <><Heading/><p>Thanks for joining!</p>;</>
  }
  return (
    <><Heading/>
    <h1>This below part is imported from formspree.  It is example for Email Submission </h1>
      <form onSubmit={handleSubmit} className="flex m-5 bg-gray-500 p-4 rounded-s-lg text-amber-300 font-bold justify-evenly" >
      <div><label htmlFor="email">
        Email Address
      </label>
      <input
        id="email"
        type="email" 
        name="email"
      />
      <ValidationError 
        prefix="Email" 
        field="email"
        errors={state.errors}
      /></div>
      <div>
        <label>Your message:<textarea name="message"></textarea>
  </label>
      <ValidationError 
        prefix="Message" 
        field="message"
        errors={state.errors}
      /></div>
      <div>
      <button type="submit" disabled={state.submitting}>
        Submit
      </button>
      </div>
    </form>
    </>
  );
}
function App() {
  return (
    <>
    {/* <Heading/> */}
    <Contact />
    </>
  );
}
export default App;