import React, {useEffect,useState} from 'react';
import axios, {Axios} from "axios" ;

const PostalAxious = () => {

  const  [userData, setUserData] = useState([])
  useEffect(()=>{
    axios.get("https://api.postalpincode.in/pincode/110001")
        .then((response)=>{
    //console.log(response)
      setUserData(response.data);

    })

  },[])
  return (
    <>
      <div className="text-center mt-9">
      <table border="1 ">
                    <tr>
                        <th>Name</th>
                        <th>Description</th>
                        <th>BranchType</th>
                        <th>DeliveryStatus</th>
                        <th>Circle</th>
                        <th>District</th>
                        <th>Division</th>
                        <th>Region</th>
                        <th>State</th>
                        <th>Country</th>
                    </tr>
          {
            (userData.map((data,index)=>{
            return(
              <>
                <tr>              
                    <td>{`${data.Name}`}</td>
                    <td>{`${data.Description}`}</td> 
                    <td>{`${data.BranchType}`}</td>                       
                    <td>{`${data.DeliveryStatus}`}</td> 
                    <td>{`${data.Circle}`}</td> 
                    <td>{`${data.District}`}</td> 
                    <td>{`${data.Division}`}</td> 
                    <td>{`${data.Region}`}</td> 
                    <td>{`${data.State}`}</td> 
                    <td>{`${data.Country}`}</td> 
              </tr>                
              </>
            );
          })

          )
          }
          </table>           
          </div>
    </>
          
  );
};

export default PostalAxious;


