import React from 'react'
import { Link } from 'react-router-dom';

const fname = "RamNaTh";
const lname = "Gopal";

const Welcom = () => {

  return (
    <div className='m-4 justify-between'>
      <h1 className='text-red-600 font-bold text-3xl'>Welcome to India Posts!</h1>
      <h1 className='text-xl text-amber-400 mt-1'>The next Gen of Postal Site </h1> 
         <div  className='flex justify-end'>

         <button className='bg-lime-500 p-2  mt-2'><Link to="/contact" className="text-amber-300 hover:text-gray-300">Get Started</Link></button>
         </div>
         <h1>This is {fname} {lname}</h1>    
         <h1>This is {`${fname} ${lname}`}</h1>      
    </div>
  )
}

export default Welcom
